@charset "utf-8";
body{
    margin: 0 auto;
    width: 1300px;
    background-color: bisque;
}
.center{
    position: relative;
}
.center h1{
    position: relative;
    display: block;
    margin: 0 auto;
    text-align: center;
    top:30px;
}
.center ul li{
    float: left;
    margin: 50px;
}

.main {
    position: absolute;
    top:200px
}
.main h2 {
    position:relative;
    text-align: center;
    text-decoration: none;
}
.main p{
    top: 100px;
    position: relative;
    text-align: center;
}
.main img{
    top:150px;
    position: relative;
    left: 50px;
}

